









































































































import goods from 'template/goods'
import login from 'auth/login'
export default {
	components: {
		goods,
		login
	},
	data() {
		return {
			banner: [],
			notice: [],
			ismask: false,
			HotsaleGoods: {},
			field: 0,
			customer_online_time: {},
			clickZone: 0,
			showPop: false,
			tipContent: '',
			showCloseBut: false,
			backTopValue: false,
			swiperCurrent: 0,
			fixSearch: false
		};
	},
	computed: {
		// ...mapState(['phoneNumber'])
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			console.log(res.target)
		}
		return {
			title: '龙腾虎跃商城',
			path: '/pages/tabbar/tabbar-1/tabbar-1',
		}
	},
	onShow() {
		this.getZone()
		this.getHotsaleGoods()
	},
	onLoad() {
		this.getBanner()
		this.getNotice()
	},
	// 监听滚动条坐标
	onPageScroll: function(e) {
		var scrollTop = e.scrollTop
		var backTopValue = scrollTop > 400 ? true : false
		var fixSearch = scrollTop > 120 ? true : false
		this.backTopValue = backTopValue
		this.fixSearch = fixSearch
	},
	methods: {
		swiperChange: function(e) {
			this.swiperCurrent = e.detail.current
		},
		//轮播图
		getBanner() {
			var that = this
			this.$api.get('/api/index/getBanner', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.banner = res.data.data
				}
			})
		},
		//公告
		getNotice() {
			var that = this
			this.$api.get('/api/notice/getList', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.notice = res.data.data
				}
			})
		},
		//热门商品
		getHotsaleGoods() {
			var that = this
			this.$api.get('/api/index/getHotsaleGoods', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.HotsaleGoods = res.data.data
				}
			})
		},
		//获取客服在线
		getZone() {
			var that = this
			this.$api.get('/api/common/getContent', {}, function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.customer_online_time = res.data.data.customer_online
				}
			})
		},

		//区域开放、客服时间提示
		goZone(id) {
			if (this.$store.state.globalData.userInfo.is_member) {
				if (id != 3) {
					uni.navigateTo({
						url: '/pages/mall/mall?field=' + id
					})
				} else {
					this.showPop = true
					this.tipContent = '当前不在工作时间范围内，请您晚点再来咨询呀~'
				}
			} else {
				this.showPop = true
				this.showCloseBut = true
				this.tipContent = '您当前还不是会员，请到个人中心注册吧~'
			}
		},
		closePop() {
			this.showPop = false,
				this.showCloseBut = false
		},

		// 滚动到顶部
		backTop: function() {
			// 控制滚动
			uni.pageScrollTo({
				scrollTop: 0
			})
		},


	}
};
